import { Component, OnInit } from '@angular/core';
import { ValidateService} from '../../services/validate.service';
import { FlashMessagesService} from 'angular2-flash-messages';
import { AuthService} from '../../services/auth.service';
import { Router} from '@angular/router';
import { SupplierService} from '../../services/supplier.service';
import { PaymentService } from 'src/app/services/payment.service';


@Component({
  selector: 'app-managesup',
  templateUrl: './managesup.component.html',
  styleUrls: ['./managesup.component.css']
})
export class ManagesupComponent implements OnInit {

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService, 
    private authService: AuthService,
    private router: Router,
    private supplierService: SupplierService,
    private paymentService: PaymentService
  ) { }
  supplierData;
  errorMsg;
  successMsg;
  ngOnInit() {
    //Get List of Suppliet from Database
    this.supplierService.getSuppliers().subscribe(
      (data : any) =>{
        this.supplierData = data
        console.log(data);
          // this.flashMessage.show('You are now register and can log in', {cssClass: 'alert-success', timeout:3000});
          // this.router.navigate(['/login']); 
      },
      error=>{
      // this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout:3000});
      // this.router.navigate(['/register']);}
      })
  }

  onSupplierSubmit(formData){
    const supplier = {
      name: formData.name,
      category: formData.category,
      bank: formData.bank,
      nuban: formData.nuban
    };
    console.log(supplier);

    //Required Field
    if(!this.validateService.validateSupplier(supplier)){
      this.errorMsg = "Please fill in the fields";
      // this.flashMessage.show('Please fill in all fields', {cssClass: 'alert-danger', timeout:3000});
       return false;
    }

    //Add Supplier
     this.supplierService.makeSupplier(supplier).subscribe(
      data =>{
        this.successMsg = "Supplier add";
          // this.flashMessage.show('You are now register and can log in', {cssClass: 'alert-success', timeout:3000});
          this.router.navigate(['/managesup']); 
      },
      error=>{
        this.errorMsg = "Something went wrong";
      // this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout:3000});
      // this.router.navigate(['/managesup']);
      }
      );
     


  }

}
