import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { FormsModule } from '@angular/forms';
import { FlashMessagesModule } from 'angular2-flash-messages';
import { HttpClientModule } from '@angular/common/http';
import { JwtModule} from '@auth0/angular-jwt';
import { Angular4PaystackModule } from 'angular4-paystack';


import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { HomeComponent } from './components/home/home.component';
import { MakepayComponent } from './components/makepay/makepay.component';
import { ManagesupComponent } from './components/managesup/managesup.component';
import { AccountsetComponent } from './components/accountset/accountset.component';

import { ValidateService} from './services/validate.service';
import { AuthService} from './services/auth.service';
import { AuthGuard} from './guards/auth.guard';
import { PaymentService} from './services/payment.service';


const appRoutes: Routes =[
  {path:'', component: HomeComponent},
  {path:'register', component: RegisterComponent},
  {path:'login', component: LoginComponent},
  {path:'dashboard', component: DashboardComponent, canActivate:[AuthGuard]},
  {path:'makepay', component: MakepayComponent, canActivate:[AuthGuard]},
  {path:'managesup', component: ManagesupComponent, canActivate:[AuthGuard]},
  {path:'accountset', component: AccountsetComponent, canActivate:[AuthGuard]}
]

export function tokenGetter() {
  return localStorage.getItem('id_token');
}

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    DashboardComponent,
    HomeComponent,
    MakepayComponent,
    ManagesupComponent,
    AccountsetComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes),
    FormsModule,
    FlashMessagesModule.forRoot(),
    JwtModule.forRoot({
      config: {
        tokenGetter: tokenGetter
      }
    }),
    Angular4PaystackModule
  ],
  providers: [ValidateService, AuthService, AuthGuard, PaymentService],
  bootstrap: [AppComponent]
})
export class AppModule { }
